.gpt3__header {
    display: flex;
    flex-direction: row;
}
.gpt3__header-content{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}
.gpt3__header-content h1{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 60px;
    line-height: 75px;
    letter-spacing: -0.04rem;
}
.gpt3__header-content p{
    font-family: var(--font-family);
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 27px;
    color: var(--color-text);
}
.gpt3__header-content_input {
    width: 100%;
    margin: 2rem 0 1rem;
    display: flex;
}
.gpt3__header-content_input input{
    flex: 2;
    width: 100%;
    min-height: 50px;
    background: var(--color-footer);
    border: 2px solid var(--color-footer);
    border-radius: 5px 0 0 5px;
    padding: 0 1rem;
    outline: none;
    color: #fff;
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 27px;
}
.gpt3__header-content_input button{
    flex: 0.6;
    width: 100%;
    min-height: 50px;
    background: #FF4802;
    border-radius: 0px 5px 5px 0;
    font-family: var(--font-family);
    font-weight: bold;
    line-height: 27px;
    color: #FFF;
    cursor: pointer;
    outline: none;
    padding: 0 1rem;
    border: none;
}
.gpt3__header-content_people {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: 2rem;
}
.gpt3__header-content_people img{
    width: 181px;
    height: 38px;
}
.gpt3__header-content_people p{
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 12px;
    line-height: 38px;
    color: #fff;
    margin: 0 0 0 1rem;
    text-align: center;
}
.gpt3__header-image {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}
.gpt3__header-image img {
    width: 100%;
    height: 100%;
}

@media screen and (max-width: 1050px) {
    .gpt3__header {
        flex-direction: column;
    }
    .gpt3__header-content{
        margin: 0 0 3rem;
    }
}
@media screen and (max-width: 650px) {
    .gpt3__header h1{
        font-size: 48px;
        line-height: 60px;
    }
    .gpt3__header p{
        font-size: 16px;
        line-height: 25px;
    }
    .gpt3__header-content_people{
        flex-direction: column;
    }
    .gpt3__header-content_people p{
        margin: 0;
    }
    .gpt3__header-content_input input,
    .gpt3__header-content_input button{
       font-size: 16px;
       line-height: 24px;
    }
}
@media screen and (max-width: 490px) {
    .gpt3__header h1{
        font-size: 37px;
        line-height: 47px;
    }
    .gpt3__header p{
        font-size: 14px;
        line-height: 25px;
    }
    .gpt3__header-content_input input,
    .gpt3__header-content_input button{
       font-size: 12px;
       line-height: 16px;
    }
}